.ListHeader-row {
  height: 40px;
  display: flex;
  align-items: center;

  background-color: #f8f8f8;
  border-radius: 15px;
}

.ListHeader-item {
  padding: 0 15px;
  color: rgb(255 188 0);
}

.ListHeader-item:nth-child(2) {
  width: 23%;
}
.ListHeader-item:nth-child(3) {
  width: 30%;
}
.ListHeader-item:nth-child(4) {
  width: 17%;
}
.ListHeader-item:nth-child(5) {
  width: 15%;
}
.ListHeader-item:nth-child(6) {
  width: 15%;
}
